@font-face {
    font-family: 'Philosopher';
    src: url('./assets/fonts/Philosopher/Philosopher-Regular/Philosopher-Regular.woff2') format('woff2'),
    url('./assets/fonts/Philosopher/Philosopher-Regular/Philosopher-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Philosopher';
    src: url('./assets/fonts/Philosopher/Philosopher-Bold/Philosopher-Bold.woff2') format('woff2'),
    url('./assets/fonts/Philosopher/Philosopher-Bold/Philosopher-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Philosopher';
    src: url('./assets/fonts/Philosopher/Philosopher-Italic/Philosopher-Italic.woff2') format('woff2'),
    url('./assets/fonts/Philosopher/Philosopher-Italic/Philosopher-Italic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}


* {
    margin: 0;
    padding: 0;

    box-sizing: border-box;
}

.app {
    min-height: 100vh;
    height: 100%;

    width: 100vw;

    display: flex;
    align-items: center;
    justify-content: center;

    background: linear-gradient(-45deg, #FAFAFA, #FFEB3B, #8E24AA, #FF7043);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;

    font-family: 'Philosopher', sans-serif;
    font-weight: 400;
    font-style: normal;
}

.board-title {
    text-align: center;

    margin: 0 0 30px 0;

    font-size: 22px;
}

.board-title span {
    font-weight: bold;
}

.board {
    display: grid;
    grid-template-columns: repeat(8, minmax(0, 64px));
    grid-auto-rows: 64px;

    position: relative;
    z-index: 3;
}

.board-wrapper {
    position: relative;
}

.board-wrapper::before {
    content: '';
    position: absolute;
    z-index: 2;
    top: -6px;
    left: -6px;

    width: calc(100% + 12px);
    height: calc(100% + 12px);

    background-color: #8D6E63;
}

.board-wrapper::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: -12px;
    left: -12px;

    width: calc(100% + 24px);
    height: calc(100% + 24px);

    background-color: #4E342E;
}

.cell {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 2px;

    color: #d5d511;
}

.cell.selected {
    background-color: #8D6E63;
}

.available {
    width: 8px;
    height: 8px;

    border-radius: 50%;

    background-color: #C5E1A5;
}

.cell img {
    position: relative;

    width: 100%;
    height: 100%;

    object-fit: cover;

    border-radius: 50%;

    border: 2px solid #FF6F00;

}

.black {
    background-color: rgba(1, 1, 1, 0.91);
}

.white img{
}

.white {
    background-color: #fff;
}

.aside {
    position: fixed;
    top: 0;
    height: 100%;

    background-color: rgba(256, 256, 256, .2);
}

.aside-left {
    left: 0;
}

.aside-right {
    right: 0;
}

.aside__wrapper {
    width: 250px;

    padding: 30px 15px;
}

.board-lost__title {
    text-align: center;
}

.board-lost__content {
    margin: 15px 0 0 0;
}

.board-lost__list{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    gap: 15px;
}

.board-lost__item{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    padding: 5px 10px;

    border: 1px solid rgba(0,0,0,.6);

    border-radius: 4px;
}

.board-lost__item-name{
    font-size: 14px;
}

.board-lost__item-img{
    width: 30px;
    height: 30px;
}

.board-lost__item-img img{
    width: 100%;
    height: 100%;

    object-fit: contain;

    border-radius: 50%;

    object-position: center center;
}

@media screen and (max-width: 600px) {
    .board {
        grid-template-columns: repeat(8, minmax(0, 40px));
        grid-auto-rows: 40px;
    }

    .aside{
        height: 100px;
        width: 100%;
    }

    .aside-left{
        top: 0;
    }
    .aside-right{
        bottom: 0;
        top: unset;
    }

    .aside__wrapper{
        width: 100%;

        padding: 5px;
    }

    .board-lost{
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    .board-lost__title{
        font-size: 12px;
    }

    .board-lost__list{
        grid-template-columns: repeat(6,minmax(0,1fr));
    }

    .board-lost__content{
        margin: 0;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}